//! moment.js locale configuration
//! locale : Konkani Devanagari script [gom-deva]
//! author : The Discoverer : https://github.com/WikiDiscoverer

import moment from '../moment';

function processRelativeTime(number, withoutSuffix, key, isFuture) {
    var format = {
        s: [' ', ' '],
        ss: [number + ' ', number + ' '],
        m: [' ', ' '],
        mm: [number + ' ', number + ' '],
        h: [' ', ' '],
        hh: [number + ' ', number + ' '],
        d: [' ', ' '],
        dd: [number + ' ', number + ' '],
        M: [' ', ' '],
        MM: [number + ' ', number + ' '],
        y: [' ', ' '],
        yy: [number + ' ', number + ' '],
    };
    return isFuture ? format[key][0] : format[key][1];
}

export default moment.defineLocale('gom-deva', {
    months: {
        standalone: '___________'.split(
            '_'
        ),
        format: '___________'.split(
            '_'
        ),
        isFormat: /MMMM(\s)+D[oD]?/,
    },
    monthsShort: '._.__.___._._._._._.'.split(
        '_'
    ),
    monthsParseExact: true,
    weekdays: '______'.split('_'),
    weekdaysShort: '._._._._._._.'.split('_'),
    weekdaysMin: '______'.split('_'),
    weekdaysParseExact: true,
    longDateFormat: {
        LT: 'A h:mm []',
        LTS: 'A h:mm:ss []',
        L: 'DD-MM-YYYY',
        LL: 'D MMMM YYYY',
        LLL: 'D MMMM YYYY A h:mm []',
        LLLL: 'dddd, MMMM Do, YYYY, A h:mm []',
        llll: 'ddd, D MMM YYYY, A h:mm []',
    },
    calendar: {
        sameDay: '[] LT',
        nextDay: '[] LT',
        nextWeek: '[] dddd[,] LT',
        lastDay: '[] LT',
        lastWeek: '[] dddd[,] LT',
        sameElse: 'L',
    },
    relativeTime: {
        future: '%s',
        past: '%s ',
        s: processRelativeTime,
        ss: processRelativeTime,
        m: processRelativeTime,
        mm: processRelativeTime,
        h: processRelativeTime,
        hh: processRelativeTime,
        d: processRelativeTime,
        dd: processRelativeTime,
        M: processRelativeTime,
        MM: processRelativeTime,
        y: processRelativeTime,
        yy: processRelativeTime,
    },
    dayOfMonthOrdinalParse: /\d{1,2}()/,
    ordinal: function (number, period) {
        switch (period) {
            // the ordinal '' only applies to day of the month
            case 'D':
                return number + '';
            default:
            case 'M':
            case 'Q':
            case 'DDD':
            case 'd':
            case 'w':
            case 'W':
                return number;
        }
    },
    week: {
        dow: 0, // Sunday is the first day of the week
        doy: 3, // The week that contains Jan 4th is the first week of the year (7 + 0 - 4)
    },
    meridiemParse: /|||/,
    meridiemHour: function (hour, meridiem) {
        if (hour === 12) {
            hour = 0;
        }
        if (meridiem === '') {
            return hour < 4 ? hour : hour + 12;
        } else if (meridiem === '') {
            return hour;
        } else if (meridiem === '') {
            return hour > 12 ? hour : hour + 12;
        } else if (meridiem === '') {
            return hour + 12;
        }
    },
    meridiem: function (hour, minute, isLower) {
        if (hour < 4) {
            return '';
        } else if (hour < 12) {
            return '';
        } else if (hour < 16) {
            return '';
        } else if (hour < 20) {
            return '';
        } else {
            return '';
        }
    },
});
