//! moment.js locale configuration
//! locale : Hebrew [he]
//! author : Tomer Cohen : https://github.com/tomer
//! author : Moshe Simantov : https://github.com/DevelopmentIL
//! author : Tal Ater : https://github.com/TalAter

import moment from '../moment';

export default moment.defineLocale('he', {
    months: '___________'.split(
        '_'
    ),
    monthsShort: '___________'.split(
        '_'
    ),
    weekdays: '______'.split('_'),
    weekdaysShort: '______'.split('_'),
    weekdaysMin: '______'.split('_'),
    longDateFormat: {
        LT: 'HH:mm',
        LTS: 'HH:mm:ss',
        L: 'DD/MM/YYYY',
        LL: 'D []MMMM YYYY',
        LLL: 'D []MMMM YYYY HH:mm',
        LLLL: 'dddd, D []MMMM YYYY HH:mm',
        l: 'D/M/YYYY',
        ll: 'D MMM YYYY',
        lll: 'D MMM YYYY HH:mm',
        llll: 'ddd, D MMM YYYY HH:mm',
    },
    calendar: {
        sameDay: '[ ]LT',
        nextDay: '[ ]LT',
        nextWeek: 'dddd [] LT',
        lastDay: '[ ]LT',
        lastWeek: '[] dddd [ ] LT',
        sameElse: 'L',
    },
    relativeTime: {
        future: ' %s',
        past: ' %s',
        s: ' ',
        ss: '%d ',
        m: '',
        mm: '%d ',
        h: '',
        hh: function (number) {
            if (number === 2) {
                return '';
            }
            return number + ' ';
        },
        d: '',
        dd: function (number) {
            if (number === 2) {
                return '';
            }
            return number + ' ';
        },
        M: '',
        MM: function (number) {
            if (number === 2) {
                return '';
            }
            return number + ' ';
        },
        y: '',
        yy: function (number) {
            if (number === 2) {
                return '';
            } else if (number % 10 === 0 && number !== 10) {
                return number + ' ';
            }
            return number + ' ';
        },
    },
    meridiemParse: /"|"| | | ||/i,
    isPM: function (input) {
        return /^("| |)$/.test(input);
    },
    meridiem: function (hour, minute, isLower) {
        if (hour < 5) {
            return ' ';
        } else if (hour < 10) {
            return '';
        } else if (hour < 12) {
            return isLower ? '"' : ' ';
        } else if (hour < 18) {
            return isLower ? '"' : ' ';
        } else {
            return '';
        }
    },
});
