//! moment.js locale configuration
//! locale : Malayalam [ml]
//! author : Floyd Pink : https://github.com/floydpink

import moment from '../moment';

export default moment.defineLocale('ml', {
    months: '___________'.split(
        '_'
    ),
    monthsShort: '._._._.___._._._._._.'.split(
        '_'
    ),
    monthsParseExact: true,
    weekdays: '______'.split(
        '_'
    ),
    weekdaysShort: '______'.split('_'),
    weekdaysMin: '______'.split('_'),
    longDateFormat: {
        LT: 'A h:mm -',
        LTS: 'A h:mm:ss -',
        L: 'DD/MM/YYYY',
        LL: 'D MMMM YYYY',
        LLL: 'D MMMM YYYY, A h:mm -',
        LLLL: 'dddd, D MMMM YYYY, A h:mm -',
    },
    calendar: {
        sameDay: '[] LT',
        nextDay: '[] LT',
        nextWeek: 'dddd, LT',
        lastDay: '[] LT',
        lastWeek: '[] dddd, LT',
        sameElse: 'L',
    },
    relativeTime: {
        future: '%s ',
        past: '%s ',
        s: ' ',
        ss: '%d ',
        m: ' ',
        mm: '%d ',
        h: ' ',
        hh: '%d ',
        d: ' ',
        dd: '%d ',
        M: ' ',
        MM: '%d ',
        y: ' ',
        yy: '%d ',
    },
    meridiemParse: /|| ||/i,
    meridiemHour: function (hour, meridiem) {
        if (hour === 12) {
            hour = 0;
        }
        if (
            (meridiem === '' && hour >= 4) ||
            meridiem === ' ' ||
            meridiem === ''
        ) {
            return hour + 12;
        } else {
            return hour;
        }
    },
    meridiem: function (hour, minute, isLower) {
        if (hour < 4) {
            return '';
        } else if (hour < 12) {
            return '';
        } else if (hour < 17) {
            return ' ';
        } else if (hour < 20) {
            return '';
        } else {
            return '';
        }
    },
});
